body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root{
  --light-3: #e1e8ef;
  --light-2: #ebeef3;
  --light-1: #f9fafc;
  --dark-3: #262626;
  --dark-2:#b7c2ce;
  --dark-1: #ebeef3;
  --grayscale: grayscale(100%); 
}


.index-at-top{
  position: relative;
  z-index: 50;
}

h1{
  font-family: sans-serif;
  font-size: 3rem;
  font-weight: 100;
}

h2{
  font-family: sans-serif;
  font-size: 2rem;
  font-weight: 100;
  text-transform: uppercase;
}
h3{
  font-family: sans-serif;
  font-size: 1.5rem;
  font-weight: 100;
  letter-spacing: 0.15rem;
}
p{
  font-size: 1rem;
  font-weight: 100;
}
button{
  padding: 15px 30px;
  background-color: #e1e8ef;
  background-color: var(--light-3);
  color: #262626;
  color: var(--dark-3);
  font-size: 1rem;
  font-weight: 100;
}
.read-all-button,
a.read-all-button{
  font-size: 0.9rem;
  letter-spacing: 0.3rem;
  font-weight: 100;
  -webkit-align-self: flex-end;
  align-self: flex-end;
  text-decoration: none;
  color: #262626;
  color: var(--dark-3);
}

a.read-all-button:hover{
text-decoration: none;
} 

img{
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}
